import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'Index',
      component: ()=>import('../components/index')
    },
    {
      path: '/404',
      name: '404',
      component: ()=>import('../components/404')
    },
    {
      path:'/login',
      name:'login',
      component: ()=>import('../components/login')
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



