export const ajax = require('axios').default
import Qs from 'qs'

ajax.defaults.baseURL='http://localhost:8081/'

export function addInterceptors(vue){
  ajax.interceptors.request.use(config => {
    config.headers.author = 'cookie'
    config.data = Qs.stringify(config.data)
    return config
  },err => {
    vue.$message({
      type:'error',
      message:'请求出错'
    })
    return Promise.reject(err)
  })

  ajax.interceptors.response.use(res => {
    return res.data
  },err => {
    vue.$message({
      type:'error',
      message:'服务器出错'
    })
    return Promise.reject(err)
  })
}

export default ajax



